.Flex-container {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.Flex-container-row {
  text-align: center;
  display: flex;
  flex-direction: row;
}

.Learn-more-button {
  font-size: 25px;
  padding: 20px;
  width: 200px;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 30px;
  border-color: #379000;
  color: #123dfb;
  background: #a2ff96;
  animation: rainbow infinite 1s linear;
}

.Basic-button {
  font-size: 20px;
  padding: 20px;
  width: 200px;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 20px;
  border-radius: 16px;
  border-color: #370000;
  color: #623afb;
  background: #f8cf66;
  animation: trans-am infinite 1s linear;
}

.Basic-plus-button {
  font-size: 20px;
  padding: 20px;
  width: 200px;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 20px;
  border-radius: 16px;
  border-color: #370000;
  color: #623afb;
  background: #f8cf66;
  animation: trans-am infinite 0.9s linear;
}

.Basic-image {
  margin-right: 20px;
  margin-top: 30px;
  margin-bottom: 30px;
  animation: orbit infinite 3.1s linear;
}

.Basic-plus-image {
  margin-left: 20px;
  margin-top: 30px;
  margin-bottom: 30px;
  animation: orbit-rev infinite 2.93s linear;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.Logo-footer {
  margin-top: 20px;
  animation: slide infinite 3s linear;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes rainbow {
  0% {
    background-color: red;
    color: green;
  }
  25% {
    background-color: yellow;
    color: red;
  }
  50% {
    background-color: blue;
    color: yellow;
  }
  100% {
    background-color: green;
    color: red;
  }
}

@keyframes trans-am {
  from { transform: scale(0.5); }
  to { transform: scale(1.5); }
}

@keyframes orbit {
  from { transform: rotate(0deg) translateX(25px) rotate(0deg); }
  to { transform: rotate(360deg) translateX(25px) rotate(-360deg); }
}

@keyframes orbit-rev {
  from { transform: rotate(360deg) translateX(25px) rotate(-360deg); }
  to { transform: rotate(0deg) translateX(25px) rotate(0deg); }
}

@keyframes slide{
  0% { transform: translateX(-200px) }
  25% { transform: translateX(-100px) }
  50% { transform: translateX(100px) }
  75% { transform: translateX(-100px) }
  100% { transform: translateX(-200px) }
}

@keyframes flash-text {
  from {color: black;}
  to {color: blue;}
}

@keyframes flash-bg {
  from {background-color: aqua;}
  to {background-color: wheat;}
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
